.techs{
  background-color: #F5F5F5;
  width: 100%;
}
.techs__container{
  min-height: 625px;
  box-sizing: border-box;
  padding: 100px 70px;
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
}
.techs__title {
  font-size: 22px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: -0.04em;
  margin: 0;
  margin-bottom: 23px;
}
.techs__line {
  width: 100%;
  height: 1px;
  background-color: #000000;
  margin-bottom: 90px;
}
.techs__subtitle {
  font-size: 50px;
  font-weight: 400;
  line-height: 58px;
  letter-spacing: -0.04em;
  text-align: center;
  margin-bottom: 27px;
}
.techs__description{
  font-size: 14px;
  line-height: 20px;
  letter-spacing: -0.04em;
  text-align: center;
  max-width: 460px;
  margin: 0 auto 100px auto;
}
.techs__list {
  list-style: none;
  display: flex;
  justify-content: center;
  gap: 10px;
  margin: 0;
  padding: 0;
}
.techs__item {
  width: 90px;
  height: 60px;
  background-color: #E8E8E8;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: -0.04em;
}

@media (max-width: 800px){
  .techs__title {
    margin-bottom: 21px;
    font-size: 22px;
  }
  .techs__container{
    padding: 90px 50px;
    min-height: 567px;
  }
  .techs__line {
    margin-bottom: 80px;
  }
  .techs__subtitle {
    margin-bottom: 23px;
  }
  .techs__description{
    font-size: 12px;
    line-height: 18px;
    margin: 0 auto 83px auto;
  }
  .techs__list {
    flex-wrap: wrap;
    max-width: 366px;
    margin: 0 auto;
    justify-content: start;
  }
  .techs__item {
    width: 84px;
    height: 57px;
    font-size: 12px;
    line-height: 15px;
  }
}
@media (max-width: 500px){
  .techs__list {
    max-width: 272px;
  }
}
@media (max-width: 450px) {
  .techs{
    background-color: #ffffff;
  }
  .techs__title {
    margin-bottom: 26px;
    font-size: 18px;
  }
  .techs__container{
    padding: 70px 18px;
    min-height: 651px;
  }
  .techs__line {
    margin-bottom: 60px;
  }
  .techs__subtitle {
    margin-bottom: 24px;
    line-height: 36px;
    font-size: 30px;
  }
  .techs__description{
    font-size: 11px;
    line-height: 16px;
    margin: 0 auto 50px auto;
  }
  .techs__list {
    max-width: 178px;
  }
  .techs__item {
    width: 84px;
    height: 57px;
    font-size: 12px;
    line-height: 15px;
  }
}